import  React from "react";
import Link from "react-router-dom";


class MovieDetail extends React.Component{
    componentDidMount(){
        const {location, history, match} = this.props;
        console.log(history)
        if(location.state == undefined){
            history.push("/")
        }
    }

    goBack  = (history) =>{
        console.log(history)
        history.goBack();
    }

    render(){
        const {location, history, match} = this.props;
        
        if(location.state){
            return (
                <div>                    
                <span>{location.state.title}</span>
                <div><span onClick={ () => {this.goBack(history)}}>Go back</span></div>
                </div>
            )
        }else{
            return null;
        }
        
    }
}

